.itemDay {
  border: 2px solid lightgray;
  border-radius: 10px;
  width: 500px;
  margin: auto;
}

p {
  font-size: 25px;
  margin-bottom: 10px;
}

.date{
  text-align: center;
  border: 2px solid lightgray;

}

.weatherSection{
  padding: 30px 10px 10px;
  border-radius: 10px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  /*box-shadow: 0 8px 6px -6px black*/
  /*background-color: rgba(54, 25, 25, .00004);*/

  /*background-size: cover;*/
}

.weatherSection2{
  z-index: -1;
  opacity: 0.5;
}

.left {
  margin-right: 30px;
}

.right {
  text-align: left;
}

.nameTime {
  text-align: center;
  margin-bottom: 10px;
}

.blockFlex {
  display: flex;
  justify-content: space-around;
}

.conditionText {
